var token = getFromLocalStorage("token");
var idKaltura = getFromLocalStorage("id_entry");
var id_perfil = getFromLocalStorage("id_perfil");
var idFilmSerie = getFromLocalStorage("id");
var positionLocal = getFromLocalStorage("position");
var tipo = getFromLocalStorage("tipo");
var title = getFromLocalStorage("titulo");
var episode = getFromLocalStorage("episodio");
var durationFilmSerie = getFromLocalStorage("duration");
var filmSerie = {};
var filmSerieTemps = {};
var filmSerieEpisodes = {};
var textTracksMode = false;
var kaltura = {};
var cursor = 3;
var showControls;
var horaNow;
var horaNext;
var inEPG = false;
var inArrow = false;
var cursorEPG = 0;
let timeNow = 0;
var currentTimeLive = 14400;
var timeInterval;
var showAudioTrackModal = false;
var modalAudioCount = 0;
var languageOptions = [];
var timeoutID;
var onModal = false;
var appOffline = false;
var kalturaPlayer2;
var idVideo = localStorage.getItem("id_entry");
var positionVideo = localStorage.getItem("position");
var liveType = localStorage.getItem("tipo");
let oldx = 0;
let oldy = 0;
let mouseTimeout;
let system = getFromLocalStorage("system");
if (!token) goTo("../../index.html");
let digitNumberTimeOut = null;
let digitNumber = null;

const displayNumberContainer = document.getElementById(
  "digited-channel-container"
);

displayNumberContainer.style.display = "none";

var kalturaPlayer2;
var idVideo = localStorage.getItem("id_entry");
var positionVideo = localStorage.getItem("position");
var liveType = localStorage.getItem("tipo");
let headers = {
  headers: {
    Authorization: "Bearer " + token,
    "Content-Type": "application/json",
  },
};

try {
  if (isLive(tipo)) {
    kalturaPlayer2.loadMedia({ entryId: idVideo }, { startTime: 0 });
  } else {
    kalturaPlayer2.loadMedia(
      { entryId: idVideo },
      { startTime: parseInt(positionVideo) }
    );
  }
  setTimeout(() => {
    let loading = document.getElementById("loading-container");
    loading.style.display = "none";
  }, 2000);
} catch (e) {
  console.error(e.message);
}

async function main() {
  createWindowVariables();
  let position = parseInt(positionLocal);

  if (!isLive(tipo)) {
    let data = await postDetails(
      "https://watch.tv.br/title?id_perfil=" +
      id_perfil +
      "&id=" +
      idFilmSerie +
      "&tipo=" +
      tipo +
      "&token=" +
      token
    );
    filmSerie = data;
  }
  await setupKalturaPlayer();
  kaltura = kalturaPlayer2;

  setTimeout(() => {
    let loading = document.getElementById("loading-container");
    loading.style.display = "none";
  }, 2000);
  setTimeout(() => {
    setTimes();
  }, 2500);

  setInterval(() => {
    settingCurrentTimeHTML();
    settingProgressBarHTML();
  }, 1000);


  buildTitlesPlayer();

  let detailsbtn = document.getElementById("details-button");
  detailsbtn.style.background;

  addClass("details-button", "unselected");
  addClass("button-epg", "unselected");
  addClass("button-back-zero", "unselected");
  addClass("back-button", "unselected");
  addClass("pause-button", "unselected");
  addClass("pause-button", "selected");
  addClass("foward-button", "unselected");
  addClass("live-button", "unselected");
  addClass("audio-menu-button", "unselected");

  let modal = document.getElementById("audio-modal");
  modal.style.display = "none";

  kaltura.addEventListener(kaltura.Event.Core.ENDED, function (event) {
    if (!filmSerie.temporadas) {
      goBack();
    } 
    else {
      let playingNow;
      let actualSeason;
      let actualEpisode;
      filmSerie.temporadas.find((season, seasonIndex) => {
        season.videos.find((episode, episodeIndex) => {
          if (episode.id_kaltura === idVideo) {
            playingNow = episode;
            actualSeason = seasonIndex;
            actualEpisode = episodeIndex;
          }
        });
      });

      let nextEp;
      if (
        filmSerie.temporadas[actualSeason].videos.length >
        actualEpisode + 1
      ) {
        nextEp = filmSerie.temporadas[actualSeason].videos[actualEpisode + 1];
      } else if (filmSerie.temporadas.length > actualSeason + 1) {
        nextEp = filmSerie.temporadas[actualSeason + 1].videos[0];
      } else {
        goBack();
        return;
      }
      settingIdsWatchSerie(nextEp);
      goTo("./index.html");

      arrowRight();
    }
  });

  setInterval(saveWatchProgress, 5000);

  if (isLive(tipo)) {
    document.getElementById("initial-time").style.display = "none";
    document.getElementById("final-time").style.display = "none";
  }

  setInterval(checkUser, 5000);
  window.onunload = userCloseApp;
}

function goBack() {
  history.back();
}

window.onload = function () {
  hideControls();
  addEventListener("mousemove", (event) => {
    clearInterval(timeInterval);
    showControls = true;
    hideControlsHTML();
    hideControls();
  });
  document.addEventListener("keydown", function (e) {
    switch (e.keyCode) {
      case 8:
      case 10009:
      case 10182:
      case 413:
        e.preventDefault();
        goBack();
        break;
      case 19:
        e.preventDefault();
        showControls = true;
        hideControlsHTML();
        pause();
        changeIconPause();
        break;
      case 415:
        e.preventDefault();
        showControls = true;
        hideControlsHTML();
        play();
        changeIconPlay();
        break;
      case 463:
      case 10252:
        e.preventDefault();
        showControls = true;
        hideControlsHTML();
        playPause();
        changeIconPlayPause();
        break;
      case 417:
        e.preventDefault();
        showControls = true;
        hideControlsHTML();
        forward();
        break;
      case 412:
        e.preventDefault();
        showControls = true;
        hideControlsHTML();
        rewind();
        break;
      case 461:
        window.history.back();
        break;
      case 37:
        e.preventDefault();
        arrowLeft();
        break;
      case 38:
        e.preventDefault();
        arrowUp();
        break;
      case 39:
        e.preventDefault();
        arrowRight();
        break;
      case 40:
        e.preventDefault();
        arrowDown();
        break;
      case 13:
        e.preventDefault();
        enterAction();
        break;
      case 48:
      case 96:
        enterChannelNumber("0");
        break;
      case 49:
      case 97:
        enterChannelNumber("1");
        break;
      case 98:
      case 50:
        enterChannelNumber("2");
        break;
      case 51:
      case 99:
        enterChannelNumber("3");
        break;
      case 52:
      case 100:
        enterChannelNumber("4");
        break;
      case 53:
      case 101:
        enterChannelNumber("5");
        break;
      case 54:
      case 102:
        enterChannelNumber("6");
        break;
      case 55:
      case 103:
        enterChannelNumber("7");
        break;
      case 56:
      case 104:
        enterChannelNumber("8");
        break;
      case 57:
      case 105:
        enterChannelNumber("9");
        break;
    }
  });

  window.addEventListener("auxclick", function (e) {
    if (e.button == 1) {
      enterAction();
    }
  });

  window.addEventListener("wheel", function (event) {
    if (event.deltaY < 0) {
      arrowUp();
    } else if (event.deltaY > 0) {
      arrowDown();
    }
  });

  document.addEventListener("keyup", function (e) {
    clearTimeout(timeoutID);
    timeoutID = setTimeout(() => {
      showControls = false;
      hideControlsHTML();
    }, 5000);
  });

  if (isLive(tipo)) {
    arrowRight();
  }
};

function arrowRight() {
  if (onModal == false) {
    if (inEPG) {
      inArrow = false;
      navegationOnEPG();
    } else {
      let count = document
        .getElementById("controls-container")
        .getElementsByTagName("button").length;
      tipo === "LIVE"
        ? setCursor(cursor + 1 <= 6, 1)
        : setCursor(cursor + 1 <= count, 1);
      navegationOnPage();
      clearInterval(timeInterval);
      showControls = true;
      hideControlsHTML();
    }
  }
}

function arrowLeft() {
  if (onModal == false) {
    if (inEPG) {
      inArrow = true;
      navegationOnEPG();
    } else {
      setCursor(cursor - 1 >= 0, -1);
      navegationOnPage();
      clearInterval(timeInterval);
      showControls = true;
      hideControlsHTML();
    }
  }
}

function arrowUp() {
  if (onModal == false) {
    if (inEPG) {
      if (inArrow == false) {
        setCursorEPG(cursorEPG - 1 >= 0, -1);
        navegationOnEPG(1);
      }
    } else {
      if (showAudioTrackModal) {
        if (
          modalAudioCount + 1 <=
          kaltura.getTracks(kaltura.Track.AUDIO).length
        ) {
          changeAudioButton(-1);
        }
      } else {
        if (cursor > 0) {
          cursor = 0;
          navegationOnPage();
          clearInterval(timeInterval);
          showControls = true;
          hideControlsHTML();
        }
      }
    }
  }
}

function arrowDown() {
  if (onModal == false) {
    if (inEPG) {
      if (inArrow == false) {
        let count = document.getElementsByClassName("popup--row").length - 1;
        setCursorEPG(cursorEPG + 1 <= count, 1);
        navegationOnEPG(-1);
      }
    } else {
      if (showAudioTrackModal) {
        if (modalAudioCount - 1 >= 0) {
          changeAudioButton(1);
        }
      } else {
        if (cursor == 0) {
          isLive(tipo)
            ? setCursor(cursor + 1 <= 5, 1)
            : setCursor(cursor + 1 <= 4, 1);
          navegationOnPage();
          clearInterval(timeInterval);
          showControls = true;
          hideControlsHTML();
        }
      }
    }
  }
}

function enterAction() {
  if (inEPG == true || onModal == true) {
    if (onModal == true) {
      goTo("../profile-select/index.html");
    } else {
      if (inArrow == true) {
        closeModalEPG();
      } else {
        openChannel();
      }
    }
  } else {
    showControls = true;
    hideControlsHTML();
    if (showAudioTrackModal) {
      changeAudioTrack(languageOptions.reverse()[modalAudioCount].track);
      toogleAudioTrackModal();
      arrowUp();
    } else {
      let button = document
        .getElementsByClassName("container")[0]
        .getElementsByTagName("button");
      let buttonSelect;
      for (let index = 0; index < button.length; index++) {
        if (
          button[index].className == "unselected selected" ||
          button[index].className == "live--button unselected selected" ||
          button[index].className == "details--button unselected selected"
        ) {
          buttonSelect = button[index].id;
        }
      }
      switch (buttonSelect) {
        case "details-button":
          history.back();
          break;
        case "button-epg":
          buildModalEPG();
          openModal();
          break;
        case "button-back-zero":
          backToZero();
          break;
        case "back-button":
          showControls = true;
          hideControlsHTML();
          rewind();
          break;
        case "pause-button":
          showControls = true;
          hideControlsHTML();
          playPause();
          changeIconPlayPause();
          break;
        case "foward-button":
          showControls = true;
          hideControlsHTML();
          forward();
          break;
        case "legends-button":
          toogleSubtitleTrack();
          break;
        case "audio-menu-button":
          toogleAudioTrackModal();
          break;
        case "live-button":
          goToLive();
          break;
        default:
          history.back();
          break;
      }
    }
  }
}

function openModal() {
  showControls = false;
  hideControlsHTML();
  inEPG = true;
  displayControler("container-epg", "block");
  displayControler("loading-container", "none");
  document.getElementsByClassName("popup--rows")[0].style.visibility =
    "visible";
  getIdChannel();
}

function getIdChannel() {
  let selectChanel = document.getElementsByClassName("popup--row")[0].id;
  let loop = document.getElementsByClassName("popup--row");
  for (let index = 0; index < loop.length; index++) {
    if (loop[index].id == idFilmSerie) {
      selectChanel = loop[index].id;
    }
  }
  buttonSelect(selectChanel);
}

function toogleAudioTrackModal() {
  showAudioTrackModal = !showAudioTrackModal;
  let buttonsDiv = document.getElementById("footer-player");
  let titleDiv = document.getElementById("container-title");
  let backButton = document.getElementById("details-button");
  let modal = document.getElementById("audio-modal");

  if (showAudioTrackModal) {
    buttonsDiv.style.display = "none";
    titleDiv.style.display = "none";
    backButton.style.display = "none";
    modal.style.display = "flex";
    renderAudioOptions();
  } else {
    let modal = document.getElementById("audio-modal");
    modal.innerHTML = "";
    buttonsDiv.style.display = "";
    titleDiv.style.display = "";
    backButton.style.display = "";
    modal.style.display = "none";
    modalAudioCount = 0;
  }
}

function toogleSubtitleTrack() {
  let textTracks = kaltura.getTracks(kaltura.Track.TEXT);

  if (textTracksMode) {
    textTracksMode = false;
    textTracks.forEach((element) => {
      if (element.label == "Off") kaltura.selectTrack(element);
    });
  } else {
    textTracksMode = true;
    textTracks.forEach((element) => {
      if (element.label == "POR") kaltura.selectTrack(element);
    });
  }
}

function hideControls() {
  let timeInterval = setTimeout(() => {
    showControls = false;
    hideControlsHTML();
  }, 4000);
  return timeInterval;
}

function renderAudioOptions() {
  let audioTracks = kaltura.getTracks(kaltura.Track.AUDIO);
  let count = audioTracks.length;
  let modal = document.getElementById("audio-modal");

  for (const audioTrack of audioTracks) {
    let langueString;
    switch (audioTrack._language) {
      case "eng":
        if (kalturaPlayer2.getTracks(kaltura.Track.AUDIO).length == 1) {
          langueString = "Portugus";
          break;
        } else {
          langueString = "Original";
        }
        break;
      case "por":
        langueString = "Portugus";
        break;
      default:
        break;
    }

    let div = createTagDiv();
    div.classList.add("language-option");
    div.id = `language-${count}`;

    div.onclick = () => {
      kaltura.selectTrack(audioTrack);
      toogleAudioTrackModal();
    };

    let p = createTagP();
    p.classList.add("language-option-text");

    p.innerText += langueString;
    div.appendChild(p);
    modal.appendChild(div);

    languageOptions.push({
      div: div,
      id: `language-${count}`,
      track: audioTrack,
    });

    count--;
  }

  let div = createTagDiv();
  div.classList.add("language-option");
  div.id = `language-${count}`;

  let p = createTagP();
  p.classList.add("language-option-text");

  p.innerText += "voltar";
  div.appendChild(p);
  modal.appendChild(div);
  languageOptions.push({
    div: div,
    id: `language-${count}`,
    track: "voltar",
  });
  div.style.background = "#e86041";
}

function buttonPause() {
  playPause();
  changeIconPlayPause();
}

function changeAudioButton(direction) {
  if (direction < 0) {
    modalAudioCount += 1;
  } else {
    modalAudioCount -= 1;
  }
  let audioOptionDiv = document.getElementById(`language-${modalAudioCount}`);
  audioOptionDiv.style.background = "#e86041";
  languageOptions.forEach((element) => {
    if (element.id != `language-${modalAudioCount}`) {
      element.div.style.background = "";
    }
  });
}

function navegationOnPage() {
  let button = document
    .getElementsByClassName("container")[0]
    .getElementsByTagName("button");

  switch (cursor) {
    case 0:
      removeClass("button-back-zero", "selected");
      removeClass("back-button", "selected");
      removeClass("pause-button", "selected");
      removeClass("foward-button", "selected");
      removeClass("live-button", "selected");
      removeClass("button-epg", "selected");

      removeClass("audio-menu-button", "selected");
      removeClass("legends-button", "selected");
      addClass("details-button", "selected");
      break;

    default:
      addClass(button[cursor].id, "selected");
      removeClass(button[cursor - 1].id, "selected");
      removeClass(button[cursor + 1]?.id, "selected");
      break;
  }
}

async function getList(id, size) {
  let body = {
    id,
    id_perfil: id_perfil,
    page: 1,
    size: 80,
  };

  let headers = {
    headers: {
      Authorization: "Bearer " + token,
      "Content-Type": "application/json",
    },
  };

  let list = await axios.post("https://watch.tv.br/v3/list", body, headers);

  return list.data;
}

function changeAudioTrack(audioSelection) {
  kaltura.selectTrack(audioSelection);
}

async function openChannel() {
  let listChanel = await getList(60);

  let id = document.getElementsByClassName("carousel-button")[cursorEPG].id;
  id = id.split("-");
  id = id[1];

  let found = listChanel.list.find((element) => element.id == id);
  settingIdsWatch(found);
}

async function closeModalEPG() {
  inEPG = false;
  inArrow = false;
  document.getElementsByClassName("progessBar")[cursorEPG].style.width = "0";
  document.getElementsByClassName("arrow-container")[0].style.background =
    "#2B2C37";
  document.getElementsByClassName("carousel-button")[cursorEPG].style.display =
    "none";

  cursorEPG = 0;

  displayControler("container-epg", "none");
}

async function buildModalEPG() {
  cursorEPG = 0;
  let start_date = await dateToday();
  let end_date =
    start_date.slice(0, -5) + convertTime(start_date.slice(11), "02:00");
  let body = {
    id_perfil: id_perfil,
    start_date: start_date,
    end_date: end_date,
  };

  let headers = {
    headers: {
      Authorization: "Bearer " + token,
      "Content-Type": "application/json",
    },
  };

  let request = await axios.post(
    "https://watch.tv.br/watch/api/epg/listschedules",
    body,
    headers
  );

  getInfo(request.data);
  return request.data;
}

async function getInfo(epg) {
  let epgs = epg.channels;

  const avlbChannels = await getList(60);
  const renderChannels = [];

  avlbChannels.list.map((listItem) => {
    epgs.map((epgItem) => {
      if (epgItem.id == listItem.id) {
        renderChannels.push(epgItem);
      }
    });
  });

  for (const element of renderChannels) {
    if (!document.getElementById("button-" + element.id)) {
      let body = {
        id_perfil: id_perfil,
        program:
          element.dates[0].programs[0]?.id +
          "," +
          element.dates[0]?.programs[1]?.id,
      };

      let headers = {
        headers: {
          Authorization: "Bearer " + token,
          "Content-Type": "application/json",
        },
      };

      let request = await axios.post(
        "https://watch.tv.br/watch/api/epg/listprograms",
        body,
        headers
      );

      buildSectionLive(request.data, element.image, element.id);
    }
  }
}
async function buttonSelect(element) {
  displayControler("loading-container", "none");
  document.getElementById("button-" + element).style.display = "block";
  document.getElementById("button-" + element).style.background = "#2B2C37";

  let container = document.getElementsByClassName("arrow-container");
  container[0].classList.remove("selected");
  let countRow = document.getElementsByClassName("popup--row");

  for (let index = 0; index < countRow.length; index++) {
    if (countRow[index].id == element) {
      cursorEPG = index;
      index = countRow.length;
      document.getElementsByClassName("popup--row")[cursorEPG].style.color =
        "white";

      document.getElementById("button-" + element).style.background = "#e86041";
      progressProgram();
    }
  }
  goingTo(element);
}

async function buildSectionLive(epg, imgProgram, index) {
  if (epg.program[0] && epg.program[1]) {
    let containerSectionTrack =
      document.getElementsByClassName("popup--rows")[0];

    if (!document.getElementById(index)) {
      let container = createTagDiv();
      container.classList.add("popup--row");
      container.id = index;

      let containerimg = createTagDiv();
      containerimg.classList.add("row--box--canal");

      let img = createTagImg();
      img.classList.add("row--button-arrow");
      img.src = imgProgram;

      let containerBoxNow = createTagDiv();
      containerBoxNow.classList.add("row--box--now");

      let tagTitulo = createTagP();
      tagTitulo.classList.add("row--box--titulo");
      tagTitulo.innerHTML =
        epg.program[0]?.titleFull.length >= 35
          ? epg.program[0]?.titleFull.substr(0, 30) + "..."
          : epg.program[0]?.titleFull;

      let tagHorario = createTagP();
      tagHorario.classList.add("row--box--horario");
      tagHorario.innerHTML =
        epg.program[0].time +
        " - " +
        convertTime(epg.program[0].time, epg.program[0].duration);

      let button = createTagButton();
      button.classList.add("carousel-button");
      button.onclick = () => {
        openChannel();
      };
      button.innerHTML = "Assistir";
      button.id = "button-" + index;

      let border = createTagDiv();
      border.classList.add("border");

      let progressBar = createTagDiv();
      progressBar.classList.add("progessBar");

      let boxSpace = createTagDiv();
      boxSpace.classList.add("row--box--space");

      let containerBoxNext = createTagDiv();
      containerBoxNext.classList.add("row--box--next");

      let tagTituloNext = createTagP();
      tagTituloNext.classList.add("row--box--titulo");
      tagTituloNext.innerHTML =
        epg.program[1]?.titleFull.length >= 27
          ? epg.program[1]?.titleFull.substr(0, 24) + "..."
          : epg.program[1]?.titleFull;

      let tagHorarioNext = createTagP();
      tagHorarioNext.classList.add("row--box--horario");
      tagHorarioNext.innerHTML =
        epg.program[1]?.time +
        " - " +
        convertTime(epg.program[1]?.time, epg.program[1]?.duration);

      containerimg.appendChild(img);
      container.appendChild(containerimg);
      container.appendChild(containerBoxNow);
      containerBoxNow.appendChild(tagTitulo);
      containerBoxNow.appendChild(tagHorario);
      containerBoxNow.appendChild(button);
      border.appendChild(progressBar);
      containerBoxNow.appendChild(border);

      container.appendChild(boxSpace);
      container.appendChild(containerBoxNext);
      containerBoxNext.appendChild(tagTituloNext);
      containerBoxNext.appendChild(tagHorarioNext);

      containerSectionTrack.appendChild(container);
    }
  }
}

function navegationOnEPG(valor) {
  let button = document.getElementsByClassName("carousel-button");
  let buttontwo = document.getElementById(button[cursorEPG].id);
  let buttonthree =
    document.getElementsByClassName("progessBar")[cursorEPG + valor];

  let container = document.getElementsByClassName("arrow-container");
  let idRow = document.getElementsByClassName("popup--row");

  if (inArrow == false) {
    container[0].style.background = "#2B2C37";
    buttontwo.style.background = "#e86041";
    buttontwo.style.display = "block";

    buttonthree.style.background = "#2B2C37";
    idRow[cursorEPG].style.color = "white";
    idRow[cursorEPG + valor].style.color = "gray";
    progressProgram();
    goingTo(idRow[cursorEPG].id);

    button[cursorEPG + valor].style.display = "none";
  } else {
    buttontwo.style.background = "#2B2C37";
    container[0].style.background = "#e86041";
    idRow[cursorEPG].style.color = "gray";
  }
}

function progressProgram() {
  let valueTime = document.getElementsByClassName("row--box--now")[cursorEPG];
  valueTime = valueTime.children[1].innerHTML;
  valueTime = valueTime.split("-");

  let hours = returnSecond(dateToday().slice(11), ":");
  let hoursBack = hours - returnSecond(valueTime[0], ":");
  let hoursT =
    returnSecond(valueTime[0], ":") - returnSecond(valueTime[1], ":");
  let progress = (hoursBack * 100) / hoursT;

  let containerProgress = document.getElementsByClassName("progessBar");
  containerProgress[cursorEPG].style.width = Math.abs(progress) + "%";
  containerProgress[cursorEPG].style.background = "#e86041";
}

function backToZero() {
  if (isLive(tipo)) {
    kaltura.dispatchTarget.currentTime =
      kaltura.dispatchTarget.currentTime - 14400;
  } else {
    kaltura.dispatchTarget.currentTime = 0;
  }
  let currentTime = kaltura.dispatchTarget.currentTime;
  currentTimeLive = 0;
  return currentTime;
}

function rewind() {
  if (isLive(tipo)) {
    let currentTime = kaltura.dispatchTarget.currentTime;
    let limit = timeNow - 14400;
    let targetTime = currentTime - 10;
    currentTimeLive = currentTimeLive - 10;

    if (targetTime > limit) {
      currentTime = targetTime;
      kaltura.dispatchTarget.currentTime = targetTime;
    }
    return;
  }

  kaltura.dispatchTarget.currentTime = kaltura.dispatchTarget.currentTime - 10;
}

function playPause() {
  kaltura.dispatchTarget.paused
    ? kaltura.dispatchTarget.play()
    : kaltura.dispatchTarget.pause();
}

function play() {
  if (!kaltura.dispatchTarget.paused)
    return;

  kaltura.dispatchTarget.play();
}

function pause() {
  if (kaltura.dispatchTarget.paused)
    return;

  kaltura.dispatchTarget.pause();
}

function forward() {
  let currentTime;
  if (isLive(tipo)) {
    currentTimeLive = currentTimeLive + 10;
    currentTime =
      kaltura.dispatchTarget.currentTime + 10 >= kaltura.dispatchTarget.duration
        ? (kaltura.dispatchTarget.currentTime = kaltura.dispatchTarget.duration)
        : (kaltura.dispatchTarget.currentTime =
          kaltura.dispatchTarget.currentTime + 10);
  } else {
    currentTime =
      kaltura.dispatchTarget.currentTime + 10 >= kaltura.dispatchTarget.duration
        ? (kaltura.dispatchTarget.currentTime = kaltura.dispatchTarget.duration)
        : (kaltura.dispatchTarget.currentTime =
          kaltura.dispatchTarget.currentTime + 10);
  }
  return currentTime;
}

