function convertSeconds(seconds) {
  var convert = function (x) {
    return x < 10 ? "0" + x : x;
  };
  return (
    convert(parseInt(String(seconds / (60 * 60)))) +
    ":" +
    convert(parseInt(String((seconds / 60) % 60))) +
    ":" +
    convert(Math.round(seconds % 60))
  );
}

function dateToday() {
  var data = new Date();
  var dia = data.getDate();
  var mes = data.getMonth() + 1;
  var ano4 = data.getFullYear();
  var hora = data.getHours();
  var min = data.getMinutes();

  var str_data =
    ano4 +
    (mes <= 9 ? "-0" + mes : "-" + mes) +
    (dia <= 9 ? "-0" + dia : "-" + dia);
  var str_hora = hora + ":" + min;

  let hora2 = convertTime(str_hora, "00:00");

  return str_data + " " + hora2;
}

function convertTime(horaInit, horaFin) {
  let horaFinish = horaFin.split(":");
  let horaStart = horaInit.split(":");

  let secondH = horaFinish[0] * 3600 + horaStart[0] * 3600;
  let secondM = horaFinish[1] * 60 + horaStart[1] * 60;

  let time = convertSeconds(parseInt(secondH) + parseInt(secondM));
  return time.slice(0, -3);
}

function returnSecond(value, split) {
  let time = value.split(split);
  return parseInt(time[0]) * 3600 + parseInt(time[1]) * 60;
}

